@import '../../constants/variable';

.c_orderBook
{
    font-size: 13px;
    flex: 1;
    text-align: right;
    background-color: $bg-right;
    color: white;
    height: 96px;
    overflow: auto;
    
    &::-webkit-scrollbar
    {
        display: none;
    }

    .books
    {
        .order
        {

            > div
            {
                display: flex;
                align-items: center;
                cursor: pointer;
            }
            
            .type
            {
                width: 50px;
                margin-right: 10px;
                padding-left: 12px;
                text-align: left;
            }

            .list
            {
                display: grid;
                grid-template-columns: 35% 30% 35%;
                position: relative;
                height: 24px;
                padding-right: $div-gap;
                flex: 1;
                
                > div
                {
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;

                    &:first-child
                    {
                        z-index: 2;
                    }
                } 

                .percent
                {
                    position: absolute;
                    height: 16px;
                    opacity: 0.16;
                    right: 0;
                    top: 4px;
                    pointer-events: none;

                    &.buy
                    {
                        background-color: #27ca7b;
                    }

                    &.sell
                    {
                        background-color: #f44173;
                    }
                }
            }
        }

        .green
        {
            color: $font-green;
        }

        .red
        {
            color: $main-red;
        }
    }
}
