@import '../../constants/variable';

body
{
    background-color: $bg-black;
}

.co_order
{
    font-size: 14px;
    @include container;
    
    nav
    {
       @include nav;
    }

    main
    {
        display: flex;
        padding: 0 40px;

        @include rwd(ipad)
        {
            padding: 10px;
        }

        section
        {
            flex: .5;

            @include rwd(ipad)
            {
                display: none;
                width: 100%;

                &.rwd-show
                {
                    display: block;
                    flex: 1;
                }
            }

            .title
            {
                color: $modal-gray;
                background-color: $bg-right-title;
                padding: 10px 24px;
            }

            &:first-child
            {
                margin-right: $div-gap;

                @include rwd(ipad)
                {
                   margin-right: 0;
                }
            }

            .head
            {
                background-color: $bg-right;
                color: $font-gray;
                display: grid;
                padding: 10px 0 10px 72px;
                grid-template-columns: 25% 40% 35%;
                text-align: right;

                div:last-child
                {
                    padding-right: 10px;
                }
            }

            .c_orderBook
            {
                height: auto;
                box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
            }
        }
    }
}