@import '../../constants/variable';

.co_tab
{
    display: none;
    justify-content: space-between;
    padding: 10px $div-gap;
    background-color: $modal-black;
    
    @include rwd(ipad)
    {
       display: flex;
    }

    > div
    {
        color: #d9d9d9;
        flex: 1;
        text-align: center;
        border-right: #99a7af 1px solid;

        &:last-child
        {
            border: none;
        }
    }
}

.co_order
{
    .co_tab
    {
        display: none;

        @include rwd(ipad)
        {
        display: flex;
        }
    }
}